import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import "./NavBar.css";
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import HeaderBarBrand from './HeaderBarBrand';
import { isLoggedin } from "../login/CookieHandler";

class NavBar extends Component {
  constructor(props) {
    super(props);
    console.log("I NavBar", props);

    this.handleLogoutClick = this.handleLogoutClick.bind(this);
  }

  handleLogoutClick = (event) => {
      if (isLoggedin('token')) {
          console.log("Förändring nu");
          document.cookie = "token=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
      }
      this.props.hlo();
  }

  render() {
    return (
      <header>
        <Navbar collapseOnSelect expand="md" bg="mine">
          <HeaderBarBrand />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
              <NavLink exact to={"/"} className="navlink" activeClassName="active-link">
                Hub
              </NavLink>
              <NavLink exact to={"/zones"} className="navlink" activeClassName="active-link">
                Zoner
              </NavLink>
              <NavLink to={"/settings"} className="navlink" activeClassName="active-link">
                Inställningar
              </NavLink>
              <NavLink to={"/rpio"} className="navlink" activeClassName="active-link">
                Rpio
              </NavLink>
              {isLoggedin('token') ?
                <NavLink to="" onClick={this.handleLogoutClick} className="navlink" activeClassName="active-link">
                  Logga ut
                </NavLink>
              :
                <NavLink to={"/login"} className="navlink" activeClassName="active-link">
                  Logga in
                </NavLink>
              }
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </header>
  )};
}

export default NavBar;
