export const getCookie = (cname) => {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(';');

    for  (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) === ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) === 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
}

export const isLoggedin = (cname) => {
    let res = getCookie(cname);
    if (res.length > 10) {
        return true;
    } else {
        return false;
    }
}

export const createCookieOneHour = (cookieValue) => {
    let now = new Date();
    let time = now.getTime();
    time += 3600 * 1000;
    now.setTime(time);
    document.cookie =
    'token=' + cookieValue +
    '; max-age=60*60*1000' +
    '; expires=' + now.toUTCString() +
    '; path=/';
      console.log("created", new Date(), now.toUTCString(), document.cookie.split(';'));
  }