import React, { Component, lazy, Suspense } from "react";
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { withRouter } from 'react-router';
import NavBar from './components/NavBar';
import NotFound from './components/NotFound';
import './App.css';
import Login from "./login/Login";
import withAuth from './login/withAuth';
import Zone from "./hub/Zone";
import Rpio from "./rpio/RpioBoard";
import { isLoggedin } from "./login/CookieHandler";
//import ErrorBoundary from "./components/ErrorBoundary";

const Hub = withRouter(
  lazy(() => import('./hub/Hub'))
);

const Zones = withRouter(
  lazy(() => import('./hub/Zones'))
);

const Settings = withRouter(
  lazy(() => import('./hub/Settings'))
);

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
        isLogdIn: isLoggedin('token'),
        token: ''
    };
    this.handleLoginToken = this.handleLoginToken.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
  }

  handleLogout = () => {
      this.setState({
          token: '',
          isLogdIn: false
      })
  }

  handleLoginToken = (val) => {
      console.log(val);
      this.setState({
          token: val,
          isLogdIn: true
      })
      console.log(this.state, isLoggedin('token'));
  }

  render() {

    return (
      <BrowserRouter>
      <div className="allwidth">
          <NavBar
            isLogdin={this.state.isLogdIn}
            hlo={this.handleLogout} />
            <Suspense fallback={<div>Laddar...</div>}>
              <Switch>
                <Route exact path={"/"} component={Hub} />
                <Route exact path={"/zones"} component={withAuth(Zones)} />
                <Route path={"/zone/:id"} component={withAuth(Zone)} />
                <Route path={"/settings"} component={withAuth(Settings)} />
                <Route path={"/rpio"} component={withAuth(Rpio)} />
                <Route path={"/login"} render={(props)=><Login {...props} handleLoginToken={this.handleLoginToken}/>}/>

                <Route exact path="**" component={NotFound} />
              </Switch>
            </Suspense>
      </div>
      </BrowserRouter>
    );
  }
}

export default App;
