import React, { Component } from 'react';
import FormGroup from 'react-bootstrap/FormGroup';
import FormControl from 'react-bootstrap/FormControl';
import FormLabel from 'react-bootstrap/FormLabel';
import "./Login.css";
import { getCookie, createCookieOneHour } from "./CookieHandler";

const baseapi = 'http://' + window.location.hostname + ':1337';
const url = baseapi + '/login';

class Login extends Component {
  constructor(props) {
    super(props);

    console.log("I login", props, this.props.route);

    this.state = {
      email: "",
      password: "",
      token: ""
    };
    this.postLogin = this.postLogin.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
  }

  validateForm() {
    return this.state.email.length > 8 && this.state.password.length > 5;
  }

  handleLoginToken = (val) => {
    this.props.handleLoginToken(val);
  }

  handleInputChange = (event) => {
    const self = this;
    const name = event.target.name;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }

    self.setState({
       typing: false,
       [name]: event.target.value,
       typingTimeout: setTimeout(function () {
           self.postLogin(self.state);
         }, 1500)
    });
    console.log(self.state);
  }

  postLogin = (event) => {
    console.log(this.state);
    console.log(document.cookie);
    if (this.state.email.length > 8 && this.state.password.length > 5) {
        return fetch(url, {
          method: 'POST',
          body: 'column=' + this.state.email + '&value=' + this.state.password,
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(res => {
          if (res.status === 200) {
            console.log(res, res.headers);
          } else {
            const error = new Error(res.error);
            console.log(error);
            throw error;
          }
          return res.json();
        })
        .then(reply => {
            createCookieOneHour(reply.data.token);
            this.setState({ token: reply.data.token });
            this.setState.email = "";
            this.setState.password = "";
            this.handleLoginToken(reply.data.token);
            this.props.history.push('/zones');
        })
        .then(test => {
            console.log(getCookie('token'));
        })
        .catch(err => {
          console.error('Error logging in please try again', err);
        });
    }
  }


  render() {
    return (
      <div className="login-page">
      <h1>Logga in</h1>
        <form>
          <FormGroup controlId="email" bssize="large">
            <FormLabel>Email</FormLabel>
            <FormControl
              autoFocus
              type="email"
              name="email"
              value={this.state.email}
              onChange={e => this.handleInputChange(e)}
              required
            />
          </FormGroup>
          <FormGroup controlId="password" bssize="large">
            <FormLabel>Lösenord</FormLabel>
            <FormControl
              value={this.state.password}
              onChange={e => this.handleInputChange(e)}
              type="password"
              name="password"
              required
            />
          </FormGroup>
        </form>
      </div>
    );
  }
}

export default Login;
